.screen {
  display:grid;
  grid-template-columns: minmax(500px, 35%) minmax(500px, 35%);
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  margin-bottom:10px;
  justify-content: center;
  margin: auto;
}

header {
  grid-column: 1 / 3;
  text-align:left;
  font-size:24px;
  margin:50px 0;
  text-transform: uppercase;
}
footer {
  grid-column: 1 / 3;
  display: flex;
  justify-content: center;
}

.container {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-content:center;
}
.screen2 .container > div {
  display: flex;
  flex-direction: row;
  align-items: start;
  margin-bottom:50px;
}

.dropzone {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border-radius: 2px;
  border: 2px dashed  #eee;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
}

textarea {
  width:100%;
  height:300px;
  border-radius: 2px;
  border: 1px solid #ddd;
}

button {
  padding: 17px 20px;
  border-radius: 50px;
  color: #595959;
  background-color: #f1f3f4;
  font-size: 18px;
  text-transform: uppercase;
  outline: none;
  box-shadow: none;
  cursor: pointer;
  border: none;
  margin: 0 5px;
  box-shadow: 0px 0px 2px 1px rgba(0,0,0,.2);
}
button:hover {
  background-color: #eee;
}
button:active {
  box-shadow: 0px 0px 2px 1px rgba(0,0,0,.15);  
}
button[disabled] {
  cursor:not-allowed;
}

label {
  margin-bottom:10px;
}
.screen {display: none}
.screen1.active1 {
  display:grid
}
.screen2.active2 {
  display:grid;
}
.lyricsArea ul {margin:0;padding: 0;}
.lyricsArea ul li { display:flex;list-style: none;padding:10px;align-items: center;color:#aaa}
.lyricsArea ul li.done a {color:#5eca5e}
.lyricsArea ul li.active a {font-weight: bold;color:#111} 
.lyricsArea ul li i {
  font-style: normal;
  width:25px;
  display: inline-block;
}
.lyricsArea ul li a {
  flex-basis: 60%;
  color:#aaa;
}
.lyricsArea ul li input {
  width: 100px;
  margin: 0 2px;
}
:focus {
  outline:0
}
.rangeContainer {
  background-color: #f1f3f4;
  border-radius: 50px;
  padding: 17px;
  box-shadow: 0px 0px 2px 1px rgba(0,0,0,.2);
  display: flex;
  justify-content: center;
  align-items: center;
}
.rangeContainer label {margin: 0;font-size: 18px;}
.rangeContainer input {margin:0;margin-left:20px}